@forward '../../sass/templates';
@use '../../sass/variables' as v;

//Main wrapper

.about-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: v.$font-family-comfortaa;
  width: 100%;

  //About container

  .about-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: v.$mobile-s-width;
    padding-top: 5rem;
    margin-bottom: v.$component-gap;

    .introduction-container {
      display: flex;
      flex-direction: column;
      align-items: center;

      .about-title {
        width: 156px;
        @extend %title-style;
      }

      .about-info {
        text-align: center;
        font-size: v.$fs-m;
        font-weight: v.$comfortaa-weight-500;
        margin-bottom: 4rem;
        line-height: 1.6rem;
      }
    }

    //Text container

    .about-text-container {
      display: flex;
      flex-direction: column;
      gap: 1rem;

      :nth-child(4) {
        margin-bottom: 3rem;
      }

      .about-text-title {
        font-family: v.$font-family-kanit;
        font-size: v.$fs-h3;
        letter-spacing: 2px;
        margin-bottom: 1rem;
      }

      .about-text {
        @extend %Text-style;
      }
    }

    //Contact button

    .btn-container {
      display: flex;
      width: 100%;
      justify-content: center;

      a {
        height: 62px;
      }

      .contact-btn {
        @extend %Btn-style;
      }
    }
  }
}

//Media queries

//******** Breakpoint - sm 410px ********//

@media only screen and (min-width: v.$breakpoint-sm) {
  .about-container {
    width: v.$mobile-sm-width !important;
    transition: 1s ease-in-out;
  }
}

//******** Breakpoint - m 576px ********//

@media only screen and (min-width: v.$breakpoint-m) {
  .about-container {
    width: v.$mobile-m-width !important;
    transition: 1s ease-in-out;
  }
}

//******** Breakpoint - md 768px ********//

@media only screen and (min-width: v.$breakpoint-md) {
  .about-container {
    width: v.$tablet-width !important;
    transition: 1s ease-in-out;
  }
}

//******** Breakpoint - lg 992px ********//

@media only screen and (min-width: v.$breakpoint-lg) {
  .about-container {
    width: v.$desktop-m-width !important;
    transition: 1s ease-in-out;
  }
}

//******** Breakpoint - xl 1200px ********//

@media only screen and (min-width: v.$breakpoint-xl) {
  .about-container {
    width: v.$desktop-l-width !important;
    transition: 1s ease-in-out;

    .introduction-container {
      width: 100%;
    }
  }
}
