///////////////////* Font family *///////////////////

$font-family-comfortaa: 'Comfortaa', cursive;
$font-family-kanit: 'Kanit', sans-serif;

///////////////////* Font weights *///////////////////

///Comfortaa

$comfortaa-weight-300: 300;
$comfortaa-weight-400: 400;
$comfortaa-weight-500: 500;
$comfortaa-weight-600: 600;
$comfortaa-weight-700: 700;

////Kanit
/// Every weight has its own italic style

$kanit-weight-200: 200;
$kanit-weight-300: 300;
$kanit-weight-400: 400;
$kanit-weight-500: 500;
$kanit-weight-600: 600;
$kanit-weight-700: 700;

$kanit-weight-italic-italic: italic;

///////////////////* Font sizes *///////////////////

$fs-xs: 12px;
$fs-s: 14px;
$fs-m: 16px;
$fs-l: 20px;
$fs-xl: 24px;
$fs-xxl: 28px;
$fs-xxxl: 34px;

////Headers

$fs-h1: 38px;
$fs-h2: 30px;
$fs-h3: 22px;

///////////////////* Colors *///////////////////

$dark-theme-color: #212121;

$color-white-cyan: #ffffff;
$color-black: #000000;
$color-soft-black: #181818cc;
$color-gray: #3636368f;
$color-filled-gray: #464646;
$color-red: #fc4445;
$color-bright-cyan: #3feee6;
$color-moderate-cyan: #55bcc9;
$color-soft-blue: #97caef;

// Progress Bar Colors

$red: #eb816e;
$blue: #1199ff;
$purple: #9b59b6;
$orange: #ffa500;
$green: #27ae60;
$yellow: #00ff6a;
$gray: #9c9c9c;
$white-gray: #d4d4d4;

// Language Skills Colors
$pink: #dd149a;
$white: #ffeeee;
$dark-orange: rgb(241, 111, 24);

///////////////////* Breakpoints *///////////////////

$breakpoint-s: 320px;
$breakpoint-sm: 410px;
$breakpoint-m: 576px;
$breakpoint-md: 768px;
$breakpoint-lg: 992px;
$breakpoint-xl: 1200px;
$breakpoint-xxl: 1400px;

///////////////////* Variables *///////////////////

$component-gap: 5rem;
$first-top-gap: 4rem;

$content-tablet-img-me-border-height: 350px;
$content-tablet-img-me-width: 350px;

$content-desktop-img-me-border-height: 450px;
$content-desktop-img-me-width: 450px;

$mobile-s-width: 300px;
$mobile-sm-width: 390px;
$mobile-m-width: 540px;
$tablet-width: 730px;
$desktop-m-width: 940px;
$desktop-l-width: 1150px;
$desktop-xl-width: 1380px;

//Progress bar animation variables

$li-padding: 5px;
$animation-delay: 0.7s;

//Contact form widths
$contact-form-s: 280px;
$contact-form-s-inner: 240px;

$contact-form-sm: 370px;
$contact-form-sm-inner: 330px;

$contact-form-m: 520px;
